body {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    margin: 0;
  }
  
  
  #root{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  
  .calculator{
    display: flex;
    align-items: center;
    /* justify-content: center; */
    flex-direction: column;
    width: 30.5625rem;
    height: 29.5625rem;
    flex-shrink: 0;
    border-radius: 1.875rem;
    background: #FFF;
    box-shadow: -30px 20px 100px 10px rgba(251, 24, 78, 0.47);
    gap: 24px;
  }
  
  h1{
    color: #2B2B2B;
    text-align: center;
    font-family: Montserrat;
    font-size: 2.25rem;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
  }
  
  input{
    padding-left: 1rem;
    width: 18.125rem;
    height: 3.125rem;
    border: 2px solid #2B2B2B;
    flex-shrink: 0;
    color: #2B2B2B;
    font-family: Montserrat;
    font-size: 1.5rem;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    
  }
  
  .button{
    display: flex;
    gap: 1rem;
   
  }
  
  .button>button{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 3.8125rem;
    height: 3.8125rem;
    flex-shrink: 0;
    background: #2B2B2B;
    color: #FFF;
    font-size: 2rem;
  }